#main {
  display: grid;
  grid-template-columns: 350px 350px auto;
  height: 100vh;
  overflow: hidden;
}
nav {
  height: 100vh;
  box-shadow: 0 1px 5px rgba(0,0,0,.2), 0 2px 2px rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12);
  z-index: 10;
  background-color: #f7f7f7;
  overflow: scroll;
}
#edit {
  background-color: transparent;
  border: none;
  border-bottom: solid 1px #fff;
  color: #fff;
  outline: none;
  font-size: 1em;
  line-height: 25px;
}
#new-group i {
  margin: 0 5px;
  line-height: 27.5px;
}
#current header {
  background-color:  #00ae5b;
}
#current header:hover {
  background-color: #62bb46;
}
#future header {
  background-color: #f1b334;
}
#download {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
}
#download button {
  background-color: transparent;
  border-color: #f1b334;
  line-height: 35px;
  border-radius: 4px;
  font-size: 1.1em;
  padding: 5px 10px;
  transition: color .2s ease, background-color .2s ease;
  cursor: pointer;
}
#download button:hover {
  background-color: #f1b334;
  color: #fff;
}
iframe {
  height: 100vh;
  width: 100%;
  overflow-y: scroll;
  border: none;
}
