.group {
  background-color: #fff;
  position: relative;
}
.group header {
  position: relative;
  color: #fff;
  display: flex;
  border-bottom: solid 1px rgba(255,255,255,0.3);
  cursor: pointer;
  transition: background-color .2s ease;
  line-height: 28px;
  padding: 5px;
}
h2 {
  margin: 0;
  line-height: 50px;
  padding-left: 10px;
  background-color: #293e50;
  color: #fff;
}
.group header span {
  line-height: 25px;
  width: 25px;
  text-align: center;
  background: red;
  border-radius: 50%;
  font-size: .7em;
  position: absolute;
  right: 5px;
  top: 5px;
}
.bar {
   display: flex;
   align-items: center;
   justify-content: center;
}
.content {
  max-height: calc(100vh - 400px);
  overflow-y: scroll;
}
.search {
  padding: 5px 10px;
  margin: 16px 10px 0px 10px;
  width: 50%;
  border: none;
  border-bottom: solid 1px #00ae5b;
  color: #00ae5b;
  outline: none;
  font-size: 1.2em;
}
.content ul {
  list-style: none;
  padding: 0;
}
.content li {
  transition: background-color .2s ease;
  cursor: pointer;
  border-bottom: solid 1px #eee;
  line-height: 39px;
  display: grid;
  grid-template-columns:  20px auto 75px;
  padding: 0px 15px;
}
.content li input {
  margin-top: 13px;
}
.content p {
  margin: 0;
  overflow: hidden;
  transition: color .2s ease;
  cursor: pointer;
}
.content p:hover {
  color: #00ae5b;
}
.content i {
  line-height: 39px;
  width: 40px;
  text-align: center;
  font-size: 20px;
  color: #00ae5b;
  transition: color .2s ease;
  z-index: 12;
  cursor: pointer;
}
.content i:hover {
  color: #62bb46;
}
.content li:hover {
  background-color: #f8f8f8;
}
.content select {
  margin-top: 11px;
}
